.header {
  background-color: var(--white);
  position: sticky;
  top: 0;
}

.header :global(.container) {
  display: grid;
  grid-template-columns: minmax(auto, 1fr) minmax(auto, 2fr);
  justify-content: space-between;
  align-items: center;
  gap: 40px;
}

.logo {
  padding: 10px 0;
  display: block;
  font-size: 1.25rem;
  line-height: 1;
  font-family: Ubuntu, Segoe UI, system-ui, sans-serif;
  color: var(--black);
  justify-self: start;
  white-space: nowrap;
}

.navigation {
  display: flex;
  justify-content: space-between;
  gap: 16px;
}

.centralized {
  display: flex;
  gap: 16px;
  justify-self: center;
}

.link {
  display: block;
  color: var(--neutral-500);
  font-family: Roboto, Segoe UI, system-ui, sans-serif;
  padding: 12px 0;
  font-size: 1rem;
  line-height: 1;
  font-weight: 500;
  justify-self: end;
  white-space: nowrap;
}

.mobile_menu {
  display: none;
  padding: 8px;
  border: none;
}

.mobile_menu :global(.bi) {
  font-size: 1.5rem;
  line-height: 1;
}

.mobile_menu :global(.bi-list::before), .mobile_menu :global(.bi-x-lg::before) {
  display: block;
}

@media (max-width: 640px) {
  .header :global(.container) {
    grid-template-columns: auto auto;
  }

  .outside_handler {
    display: none;
  }

  .outside_handler:global(.active) {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: flex-end;
    background-color: rgb(0 0 0 / 25%);
  }

  .navigation {
    background-color: var(--black);
    display: grid;
    width: clamp(20px, 200px, 50%);
    align-content: start;
    padding: 20px;
    padding-top: 84px;
  }
  .navigation .centralized {
    display: grid;
    align-content: start;
  }

  .link {
    justify-self: unset;
    color: var(--neutral-300);
  }

  .mobile_menu {
    display: block;
    position: relative;
  }
}