.draw :global(.container) {
  display: grid;
  min-height: 536px;
}

.draw__content {
  align-self: center;
  max-width: 560px;
}

.draw__title {
  font-family: Ubuntu, Segoe UI, system-ui, sans-serif;
  font-size: 3rem;
  line-height: 1.25;
  font-weight: 700;
  color: var(--black);
  margin-bottom: 8px;
}

.draw__description {
  font-family: Roboto, Segoe UI, system-ui, sans-serif;
  font-size: 1.25rem;
  line-height: 2rem;
  color: var(--neutral-400);
  margin-bottom: 24px;
}

.why {
  background-color: var(--black);
}

.why :global(.container) {
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  min-height: 298px;
  padding: 40px;
}

.why__content {
  justify-self: end;
  max-width: 560px;
}

.why :global(.heading-2) {
  color: var(--white);
  margin-bottom: 8px;
}

.why :global(.paragraph) {
  color: var(--neutral-200);
  margin-bottom: 8px;
}

.why :global(.link) {
  color: var(--white);
  border-bottom: 3px solid var(--white);
}

.features {
  display: grid;
  padding: 40px 0;
}

.features__content {
  max-width: 560px;
  display: grid;
  gap: 16px;
}
.features__content :global(.heading-3) {
  text-align: right;
  margin-bottom: 8px;
}

.features__content :global(.paragraph) {
  text-align: right;
}

.plans {
  padding: 40px 0 40px;
}

.plans :global(.heading-2) {
  text-align: center;
  margin-bottom: 32px;
}

.plans__wrapper {
  display: flex;
  align-items: flex-start;
  gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
}

.plans__card {
  display: grid;
  gap: 20px;
  background-color: var(--neutral-100);
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0px 4px 8px -2px rgb(0 0 0 / 25%);
  width: 320px;
  flex-shrink: 0;
}

.plans__benefits_list {
  display: grid;
  gap: 16px;
}

.plans__benefit {
  font-family: Roboto, Segoe UI, system-ui, sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 500;
  color: var(--neutral-500);
  align-items: center;
  display: flex;
  gap: 8px;
}

.plans footer {
  display: grid;
  grid-template-columns: auto auto;
  justify-content: space-between;
  align-items: center;
}

.plans__price {
  font-family: Ubuntu, Segoe UI, system-ui, sans-serif;
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 1.2;
}

.call_to_action {
  background-color: var(--black);
  padding: 20px 0;
}

.call_to_action__call {
  font-family: Ubuntu, Segoe UI, system-ui, sans-serif;
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 2rem;
  color: var(--white);
  text-align: center;
  margin: 0 auto;
  max-width: 62ch;
  margin-bottom: 20px;
}

.call_to_action :global(.button) {
  margin: 0 auto;
  display: block;
  max-width: fit-content;
  background-color: var(--white);
  color: var(--black);
  white-space: nowrap;
}

@media (max-width: 640px) {
  .draw__title {
    font-size: 2.5rem;
  }

  .draw__description {
    font-size: 1rem;
  }

  .why :global(.container) {
    padding: 20px;
  }

  .plans__card {
    padding: 16px;
    gap: 12px;
    min-width: 280px;
  }

  .plans__benefits_list {
    gap: 4px;
  }

  .plans__benefit {
    font-size: 0.875rem;
    line-height: 1.5rem;
  }

  .plans__price {
    font-size: 1rem;
    line-height: 1.25;
  }

  .call_to_action__call {
    font-size: 1rem;
    line-height: 1.25;
  }
}